.input-item {
  display: flex;
  flex-direction: column;
  border: 2px solid;
  margin: 5px;
  padding: 10px;
  border-radius: 10px;
  min-width: 134px;
  justify-content: center;
  align-items: center;

  > div {
    display: flex;
    flex-direction: row;
    align-items: center;

    label {
      font-size: 14px;
    }

    input {
      width: 80px;
    }

    &:first-of-type {
      margin-bottom: 5px;
    }
  }

  .item-action-btn {
    display: flex;
    flex-direction: row;
    padding: 14px 0 0;
    width: max-content;

    button:not(:last-of-type) {
      margin-right: 10px;
    }
  }
}
