.App {
  text-align: center;
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  color: white;
  display: flex;
  flex-direction: column;
}

.input-area {
  display: flex;
  flex-direction: row;

  .action-btn {
    display: flex;
    flex-direction: column;
    min-width: max-content;

    > button {
      margin: 14px;
      min-width: max-content;
    }
  }
}

.item-list {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  flex-wrap: wrap;
  align-content: flex-start;
}

.calculated-items-container {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;

  .calculated-item {
    width: max-content;
    border: 1px solid white;
    list-style: none;
    margin: 5px;
    border-radius: 10px;

    p {
      margin: 5px;
      text-align: left;
    }
  }
}

.red {
  color: red;
}
